@import url(https://fonts.googleapis.com/css2?family=Lato:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Kaushan+Script&display=swap);
body {
    font-family: 'Lato', sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    background-color: #2C2B3C;
    color: white;
    font-size: 16px;
    margin: 0;
}

input, button {
    font-family: inherit;
    border: none;
}

button {
    cursor: pointer;
    background-color: #FF3D00;
    color: white;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

ul {
    list-style: none;
}

img {
    height: 100%;
    width: 100%;
}


.chat-messages-container {
    display: flex;
    flex-direction: column;
    margin: auto;
    width: 100%;
    max-width: 700px;
    height: 100%;
}

.chat-messages-dialog-container {
    display: flex;
    flex-direction: column;
    margin: auto;
    align-items: baseline;
    width: 100%;
    overflow-y: auto;
    height: 60vh;
    padding-right: 20px;
    scrollbar-width: thin;
}

.user-message-container, .other-user-message-container {
    display: flex;
    font-size: 16px;
    margin-top: 10px;
}

.user-message-container {
    flex-direction: column;
    max-width: 600px;
    align-self: flex-end;
    margin-right: 15px;
}

.other-user-message-container {
    flex-direction: row;
    max-width: 700px;
    align-self: flex-start;
}

.other-user-message {
    flex-direction: row;
    max-width: 600px;
}

.other-user-message-photo {
    width: 50px;
    height: 50px;
    background: white;
    border-radius: 100%;
    margin: 10px;
}

.user-message-data, .other-user-message-data {
    margin: 10px 5px 10px 0;
}

.user-message-data {
    text-align: right;
}

.user-message-text, .other-user-message-text, .send-message-input {
    border-radius: 10px;
    padding: 10px 15px;
    line-height: 20px;
    display: inline-block;
}

.user-message-text {
    background: rgba(255, 92, 0, 0.07);
}

.other-user-message-text {
    background: rgba(64, 63, 76, 0.5)
}

.send-message-container {
    margin-top: 36px;
    margin-bottom: 36px;
    align-self: center;
    width: calc(100% - 60px);
}

#send-message-form {
    width: 100%;
}

.send-message-input {
    font-family: inherit;
    background-color: #2C2B3C;
    box-shadow: inset 0px 0px 30px rgba(0, 0, 0, 0.1);
    border-radius: 10px;
    border: none;
    color: inherit;
    width: 100%;
    height: 110px;
    resize: none;
}

.send-message-submit {
    visibility: hidden;
}

.other-user-message-data p, .user-message-data p {
    margin: 0;
}

.other-user-message-data p span, .user-message-data p span {
    margin-right: 10px;
}

.other-user-message-data p span:last-child, .user-message-data p span:last-child {
    font-size: 12px;
    color: #A2A2A2;
}

.chat-messages-container-empty {
    color: #A2A2A2;
    font-size: 30px;
    font-weight: 500;
    margin: auto;
}

.chat-messages-header {
    display: flex;
    align-items: center;
    height: 30px;
    padding: 10px 15px;
    width: calc(100% - 30px);
    background-color: rgba(255, 92, 0, 0.07);
}

.chat-messages-back {
    background-color: transparent;
}

.chat-messages-avatar {
    height: 40px;
    width: 40px;
    margin-right: 20px;
}

.chat-messages-back {
    margin-left: auto;
}

.chat-messages-add {
    background-color: transparent;
    cursor: pointer;
    width: 20px;
    height: 20px;
    margin-left: 10px;
}

.chat-messages-add:hover {
    opacity: 0.3;
}

.chat-add-participant {
    display: flex;
    flex-direction: column;
    align-items: center;
    position: absolute;
    background: #2C2B3C;
    padding: 10px 20px;
    height: 100px;
    width: 400px;
    z-index: 1;
    top: 40%;
    box-shadow: 0px 0px 100px 40px rgba(0, 0, 0, 0.35);
    border-radius: 8px;
}

.chat-add-participant form {
    display: flex;
    flex-direction: row;
    width: 100%;
    grid-gap: 15px;
    gap: 15px;
}

.chat-add-participant input[type="image"] {
    background: none;
    box-shadow: none;
    height: 20px;
    width: 20px;
    padding-left: 0;
}

.chat-add-participant input[type="email"] {
    width: 100%;
    height: 40px;
}

.chat-add-participant .heading {
    font-size: 32px;
    font-weight: 600;
    margin-bottom: 10px;
}


.dialog-wrapper {
    padding-top: 50px;
    padding-right: 20px;
    box-shadow: 5px 0px 5px rgba(0, 0, 0, 0.1);
    width: 300px;
}

.dialog-input {
    width: 207px;
    height: 50px;
    background: rgba(255, 255, 255, 0.06);
    border-radius: 10px;
    margin-bottom: 2%;
    margin-left: 10px
}

.dialog-list {
    padding: 0;
    padding-left: 10px;
    display: flex;
    flex-direction: column;
    grid-gap: 10px;
    gap: 10px;
    overflow-y: auto;
    height: 67vh;
    margin-top: 0;
    padding-top: 10px;
    scrollbar-width: thin;
}

.dialog-list-item {
    display: flex;
    align-items: center;
    width: 220px;
    height: 65px;
    margin-bottom: 1%;
    background: #2C2B3C;
    box-shadow: 0px 0px 5px 1px rgba(0, 0, 0, 0.25);
    border-radius: 10px;
    list-style: none;
    padding: 15px;
    cursor: pointer;
}

.dialog-list-item:hover {
    box-shadow: 0px 0px 5px 1px rgba(0, 0, 0, 0.35);
}

.dialog-logo {
    width: 50px;
    height: 50px;
    background: white;
    border-radius: 100%;
}

.dialog-photo {
    border-radius: 100%;
    height: 60px;
    width: 60px;
    background-color: white;
}

.dialog-timestamp {
    align-self: flex-start;
    margin-left: auto;
    font-size: 11px;
    color: gray;
}

.dialog-name {
    margin-left: 20px;
}

.dialog-add {
    background-color: transparent;
    cursor: pointer;
    display: inline-flex;
    padding: 0;
    width: 30px;
    height: 30px;
}

.dialog-search-wrapper {
    display: flex;
    grid-gap: 10px;
    gap: 10px;
    align-items: center;
}

.dialog-accept,
.dialog-cancel {
    background: none;
    box-shadow: none;
    height: 18px;
    width: 18px;
    padding-left: 0;
}

.dialog-accept:hover,
.dialog-cancel:hover {
    opacity: 0.3;
}

.dialog-wrapper form {
    display: inline-flex;
    flex-direction: row;
    grid-gap: 14px;
    gap: 14px;
}

.dialog-cancel {
    background-color: transparent;
}

.chats-wrapper {
    display: flex;
    flex-direction: row;
    max-width: 1000px;
    margin: auto;
    margin-top: 0;
    height: 92vh;
}

.chats-none {
    color: #A2A2A2;
    font-size: 36px;
    font-weight: 600;
    margin: auto;
    padding-bottom: 20px;
}
.wrapper {
    margin-top: 10px;
    display: flex;
    flex-direction: column;
    grid-gap: 30px;
    gap: 30px;
}

.header {
    font-style: normal;
    font-weight: bold;
    font-size: 50px;
    color: #A2A2A2;
    text-align: center;
}

.form {
    display: flex;
    flex-direction: column;
    align-items: center;
}

form {
    display: flex;
    flex-direction: column;
    align-items: center;
    grid-gap: 26px;
    gap: 26px;
}

input {
    max-width: 400px;
    width: 40vw;
    height: 60px;

    background: rgba(255, 255, 255, 0.06);
    box-shadow: 0 0 4px rgba(0, 0, 0, 0.25);
    border-radius: 5px;

    font-style: normal;
    font-weight: normal;
    font-size: 18px;
    line-height: 20px;
    color: #A2A2A2;

    padding-left: 20px;
}

input:focus {
    outline: none;
    box-shadow: 0 0 0 1px #FF5C00;
}

.btn-signUp {
    width: 240px;
    height: 50px;
    background: #FF3D00;
    box-shadow: 0 0 4px #2C2B3C;
    border-radius: 10px;
    border-right-color: #FF3D00;

    font-size: 18px;
    line-height: 20px;
    color: #FFFFFF;
}

.p-already-register {
    color: #A2A2A2;
    font-size: 14px;
}

.p-already-register a {
    color: white;
    text-decoration: underline;
}

.error {
    color: red;
    height: 0;
    font-size:14px;
    margin-top: -20px;
    margin-bottom: -5px;
    margin-right: auto;
}

.logInWrapper {
    margin-top: 20px;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.logo {
    width: 175px;
    height: 200px;
}

.logo-text {
    font-family: 'Kaushan Script', cursive;
    font-size: 60px;
    color: #A2A2A2;
    margin-top: 0;
}

.error-invalid {
    font-size: 11px;
    color: red;
    margin: -20px;
}
.popup-overlay {
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background: rgba(0, 0, 0, 0.7);
}

.popup-container {
    margin: 70px auto;
    padding: 20px;
    background: #403F4C;
    border-radius: 20px;
    width: 250px;
    height: 500px;
    max-height: 70vh;
    position: relative;
    transition: all 3s ease-in-out;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.privacy-button {
    background: url(/static/media/padlock.b5c18757.svg) no-repeat left;
}

.change-photo-button {
    background: url(/static/media/image-icon.0b4c18ed.svg) no-repeat left;
}

.popup-option-button {
    cursor: pointer;
    padding: 10px 15px 10px 20px;
    background-size: 15px;
    border: none;
    color: inherit;
    font-size: 16px
}

.logout-button {
    color: #A2A2A2;
    border: none;
    background: none;
    font-size: 16px
}

.popup-input {
    box-shadow: 0px 0px 4px rgba(0, 0, 0, 0.25);
    width: 230px;
    height: 30px;
    font-size: 16px;
    border: none;
    padding: 10px;
    margin: 0;
    border-radius: 5px;
}

.popup-form-password {
    display: flex;
    flex-direction: column;
    align-items: center;
    grid-gap: 16px;
    gap: 16px;
    margin: auto;
}

.popup-submit {
    border-radius: 5px;
    display: flex;
    align-items: center;
    width: 120px;
    height: 40px;
    font-size: 16px;
    color: white;
    background: #FF3D00;
    padding: 0;
}

.back-button {
    background: none;
    color: #A2A2A2;
}

.popup-photo-select-btn,
input[type=file]::-webkit-file-upload-button {
    border-radius: 5px;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 200px;
    height: 40px;
    box-shadow: none;
    font-size: 16px;
    color: white;
    background: none;
    cursor: pointer;
    margin-bottom: 0;
}

.confirm, .undo {
    color: transparent;
}

.confirm {
    background: url(/static/media/accept.acf55764.svg) no-repeat center center;
}

.undo, .popup-close {
    background: url(/static/media/close.d6fc4d51.svg) no-repeat center center;
    background-size: 15px;
}

.popup-close {
    height: 40px;
    width: 40px;
    margin-left: auto;
    border: none;
}

.popup-photo {
    height: 125px;
    width: 125px;
    margin-bottom: 20px;
    border-radius: 100%;
    overflow: hidden;
}

.popup-photo img {
    object-fit: cover;
}

.popup-photo img {
    height: 100%;
    width: 100%;
}

.popup-name {
    font-size: 18px;
    font-weight: 600;
    padding-bottom: 20px
}
.popup-headline{
    font-size:22px;
    font-weight: 600;
    padding-bottom: 10px;
}

.popup-accept {
    margin-bottom: 10px;
}

header {
    background: #403F4C;
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 60px;
    padding-left: 30px;
    padding-right: 30px;
}

.header-logo {
    height: 40px;
}

.header-logo img {
    height: 100%;
    width: 100%;
}

.header-nav-list {
    display: flex;
    font-weight: 600;
    grid-gap: 20px;
    gap: 20px;
}

.header-nav-list li:hover {
    color: #FF5C00;
    cursor: pointer;
}

.header-logout {
    color: #A2A2A2;
    padding-left:20px;
}
